'use client'

import Title from '@/app/components/Admin/Title'
import React, { useState } from 'react'
import Image from 'next/image'
import { assets } from '@/app/assets/assets'

const AddTablePage = () => {
    const [image, setImage] = useState<string | File>('')
    const [table, setTable] = useState({
        number: '',
        capacity: 0,
        section: '',
        image: '',
        type: '',
        isWindow: false,
        pricePerSlot: 0,
        location: '',
        description: '',
        isAvailable: true,
        createdAt: new Date().toISOString(),
    })

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()
    }

    return (
        <div className='px-6 py-8 flex-1 bg-gray-50'>
            <Title title='Add Table' subTitle='Add a new table to the system' />

            <form onSubmit={handleSubmit} className="bg-white p-6 rounded-lg border border-gray-200 flex flex-col gap-6 text-gray-600 text-sm mt-6 max-w-xl">
                {/* Table Image */}
                <div className='flex items-center gap-4'>
                    <label htmlFor="table-image" className="cursor-pointer">
                        <div className="relative w-24 h-24 rounded-lg bg-gray-100 border-2 border-dashed border-gray-300 flex items-center justify-center hover:bg-gray-50 transition-colors">
                            {image instanceof File ? (
                                <Image
                                    src={URL.createObjectURL(image)}
                                    alt="table preview"
                                    fill
                                    className='object-fill rounded-lg'
                                />
                            ) : (
                                <Image
                                    src={assets.upload_icon}
                                    alt="upload"
                                    className='opacity-50 w-full h-full'
                                />
                            )}
                        </div>
                        <input
                            type='file'
                            id='table-image'
                            accept='image/*'
                            hidden
                            onChange={e => e.target.files?.[0] && setImage(e.target.files[0])}
                        />
                    </label>
                    <div>
                        <p className='font-medium text-gray-800'>Table Image</p>
                        <p className='text-sm text-gray-500 mt-1'>Upload a picture of the table</p>
                    </div>
                </div>

                {/* Table Details */}
                <div className='grid md:grid-cols-2 gap-4'>
                    <div>
                        <label className='block font-medium text-gray-800 mb-1'>Table Number</label>
                        <input
                            type="text"
                            placeholder="e.g. T01"
                            className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-primary'
                            value={table.number}
                            onChange={(e) => setTable({ ...table, number: e.target.value })}
                        />
                    </div>
                    <div>
                        <label className='block font-medium text-gray-800 mb-1'>Capacity</label>
                        <input
                            type="number"
                            placeholder="Number of guests"
                            className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-primary'
                            value={table.capacity || ''}
                            onChange={(e) => setTable({ ...table, capacity: parseInt(e.target.value) })}
                        />
                    </div>
                    <div>
                        <label className='block font-medium text-gray-800 mb-1'>Section</label>
                        <select
                            className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-primary'
                            value={table.section}
                            onChange={(e) => setTable({ ...table, section: e.target.value })}
                        >
                            <option value="">Select section</option>
                            <option value="Main Hall">Main Hall</option>
                            <option value="Terrace">Terrace</option>
                            <option value="Private Room">Private Room</option>
                        </select>
                    </div>
                    <div>
                        <label className='block font-medium text-gray-800 mb-1'>Type</label>
                        <select
                            className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-primary'
                            value={table.type}
                            onChange={(e) => setTable({ ...table, type: e.target.value })}
                        >
                            <option value="">Select type</option>
                            <option value="Standard">Standard</option>
                            <option value="Family">Family</option>
                            <option value="Premium">Premium</option>
                            <option value="VIP">VIP</option>
                        </select>
                    </div>
                    <div>
                        <label className='block font-medium text-gray-800 mb-1'>Price per Slot</label>
                        <input
                            type="number"
                            placeholder="Price in USD"
                            className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-primary'
                            value={table.pricePerSlot || ''}
                            onChange={(e) => setTable({ ...table, pricePerSlot: parseInt(e.target.value) })}
                        />
                    </div>
                    <div>
                        <label className='block font-medium text-gray-800 mb-1'>Location</label>
                        <select
                            className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-primary'
                            value={table.location}
                            onChange={(e) => setTable({ ...table, location: e.target.value })}
                        >
                            <option value="">Select location</option>
                            <option value="Ground Floor">Ground Floor</option>
                            <option value="First Floor">First Floor</option>
                            <option value="Rooftop">Rooftop</option>
                        </select>
                    </div>
                </div>

                {/* Additional Options */}
                <div className='space-y-3'>
                    <label className='flex items-center gap-2 cursor-pointer'>
                        <input
                            type="checkbox"
                            className='w-4 h-4 text-primary'
                            checked={table.isWindow}
                            onChange={(e) => setTable({ ...table, isWindow: e.target.checked })}
                        />
                        <span>Window View</span>
                    </label>
                    <label className='flex items-center gap-2 cursor-pointer'>
                        <input
                            type="checkbox"
                            className='w-4 h-4 text-primary'
                            checked={table.isAvailable}
                            onChange={(e) => setTable({ ...table, isAvailable: e.target.checked })}
                        />
                        <span>Available for Booking</span>
                    </label>
                </div>

                {/* Description */}
                <div>
                    <label className='block font-medium text-gray-800 mb-1'>Description</label>
                    <textarea
                        rows={3}
                        placeholder="Describe the table and its features..."
                        className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-primary'
                        value={table.description}
                        onChange={(e) => setTable({ ...table, description: e.target.value })}
                    />
                </div>

                {/* Submit Button */}
                <button
                    type="submit"
                    className='bg-primary text-white font-medium px-6 py-2 rounded-lg hover:bg-primary/90 transition-colors mt-4'
                >
                    Add Table
                </button>
            </form>
        </div>
    )
}

export default AddTablePage
